import React, { Component } from 'react'
import './LogoutPanel.css'
import Logout from "./Logout.js"

class LogoutPanel extends Component{
    render(){
        return(
            <div id="menu-panel"
                 className="center-all-flex">
                <Logout />
            </div>
        )
    }
}

export default LogoutPanel


