import React, { Component } from 'react'
import './DashboardContent.css'
import { connect } from 'react-redux'
import TermGrid from "../../../Elements/AddTerm/TermGrid"
import AddTerm from "../../../Elements/AddTerm/AddTerm"

class DashboardContent extends Component{
    render(){
        const {teacher} = this.props
        return(
            <section id="dashboard-container">

                {/*Checks for school terms & renders accordingly*/}
                {
                    teacher.terms.length!==0 ?
                        <TermGrid icon="fab fa-buromobelexperte" /> :
                        <section className="center-all-flex flex-column dashboard-empty">
                            <h2>Welcome to Homeschool Hub!</h2>
                            <div id="term-instructions"
                                 className="center-all-flex">
                                <h3>
                                    Let's begin by adding a school term.
                                </h3>
                                <AddTerm />
                            </div>
                        </section>

                }
            </section>
        )
    }
}

const mapStateToProps = (state) =>{
    return{
        teacher: state.teacherReducer.currentTeacher
    }
}

export default connect(mapStateToProps)(DashboardContent)


